import React from "react";
import styled from "styled-components";
import { course } from "../../../data/Subject";
import { useAddStudent } from "../../../Context/AddStudent";

const AddFromAnother = () => {
  const students = useAddStudent();

  const handleNext = (evt) => {
    evt.preventDefault();
    const { year, semester, courseName, term, center } = students.otherInfo;
    if (
      year !== undefined &&
      semester !== undefined &&
      courseName !== undefined &&
      term !== undefined &&
      center !== undefined
    ) {
      students.handleOtherImport(false);
    }
  };

  return (
    <Panel onSubmit={handleNext}>
      <Title>Add Student from Another Class</Title>
      <SelectGroup>
        <InputGroup>
          <Label>Year</Label>
          <Select
            name="year"
            defaultValue={students.otherInfo.year}
            onChange={(e) => {
              students.handleOtherFormImportChange(e);
              if (e.target.value === "0") {
                document.getElementsByName("semester").values = null;
                document.getElementsByName("courseName").values = null;
                delete students.otherInfo["year"];
                delete students.otherInfo["semester"];
                delete students.otherInfo["courseName"];
              }
            }}
            style={{ marginRight: "10px" }}
            required
          >
            <option value="0">Select Year</option>
            <option value="1">1st Year</option>
            <option value="2">2nd Year</option>
            <option value="3">3rd Year</option>
            <option value="4">4th Year</option>
          </Select>
        </InputGroup>
        <InputGroup>
          <Label>Semester</Label>
          <Select
            name="semester"
            onChange={(e) => {
              students.handleOtherFormImportChange(e);
              if (e.target.value === "0") {
                document.getElementsByName("courseName").values = null;
                delete students.otherInfo["semester"];
                delete students.otherInfo["courseName"];
              }
            }}
            required
          >
            {students.otherInfo.year !== undefined && (
              <>
                <option value="0">Select Semester</option>
                <option value="1">1st Semister</option>
                <option value="2">2nd Semister</option>
              </>
            )}
          </Select>
        </InputGroup>
      </SelectGroup>
      <InputGroup>
        <Label>Course</Label>
        <Select
          name="courseName"
          defaultValue={students.otherInfo.courseName}
          onChange={students.handleOtherFormImportChange}
          required
        >
          {students.otherInfo.year !== undefined &&
            students.otherInfo.semester !== undefined && (
              <option value="">Select Course Title</option>
            )}
          {students.otherInfo.year !== undefined &&
          students.otherInfo.semester !== undefined
            ? course.map((data) => {
                return data.year === Number(students.otherInfo.year) &&
                  data.semester === Number(students.otherInfo.semester) ? (
                  <option key={data.code} value={data.fullTitle}>
                    {data.fullTitle}
                  </option>
                ) : null;
              })
            : null}
        </Select>
      </InputGroup>

      <InputGroup>
        <Label>Term</Label>
        <Input
          name="term"
          type="number"
          defaultValue={students.otherInfo.term}
          onChange={students.handleOtherFormImportChange}
          placeholder="Term"
          required
        />
      </InputGroup>
      <ButtonGroup>
        <Button
          onClick={() => {
            students.handleIsImportOther(false);
          }}
          style={{ background: "rgba(255, 56, 56,1.0)" }}
        >
          Back
        </Button>
        <Button style={{ background: "rgba(72, 52, 212,1.0)" }} type="submit">
          Next
        </Button>
      </ButtonGroup>
    </Panel>
  );
};

export default AddFromAnother;

const Title = styled.h1`
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  text-align: center;
`;

const Panel = styled.form`
  display: flex;
  flex-direction: column;
  width: 550px;
  padding: 2rem;
  margin-top: 2rem;
  border-radius: 5px;
  border: 1px solid rgba(3, 3, 3, 0.2);
  @media (max-width: 560px) {
    width: 100%;
  }
`;

const Input = styled.input`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(3, 3, 3, 0.5);
  border-radius: 3px;
`;

const Select = styled.select`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(3, 3, 3, 0.5);
  border-radius: 3px;
`;

const ButtonGroup = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.5rem;
  width: 100%;
  justify-content: flex-end;

  @media (max-width: 560px) {
    flex-direction: column-reverse;
  }
`;

const Button = styled.button`
  border: none;
  padding: 0.5rem 1rem;
  font-weight: 600;
  min-width: 115px;
  color: #fff;
  margin: 1rem 0;
  font-size: 0.9rem;
  margin: 0.5rem;
  &:nth-last-child(1) {
    margin-right: 0;
  }

  @media (max-width: 560px) {
    padding: 0.5rem 1rem;
    min-width: 95%;
    margin: 0.5rem 0;
    font-size: 1.1rem;
  }
`;

const InputGroup = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 0.5rem;
`;

const Label = styled.label`
  font-size: 0.9rem;
  font-weight: 600;
`;
const SelectGroup = styled.div`
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  width: 100%;
`;
